import React, {Component} from 'react';
import {Navbar, NavbarBrand, NavbarToggler} from 'mdbreact';
import ActiveTask from "./playbook/active_task";
import connect from "react-redux/es/connect/connect";
import DomActions from "../actions/dom_actions";


class TopNavigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collapse: false,
        };
    }



    render() {
        if(!((this.props.activeTask && this.props.activeTask.task)||window.innerWidth < 1200)){
            return null
        }
        return (
            <Navbar className="flexible-navbar" light expand="md" scrolling>
                <NavbarBrand>
                    <span/>
                </NavbarBrand>
                <NavbarToggler onClick = { this.props.toggleShowSideNav} />
                <ActiveTask/>
            </Navbar>
        );
    }
}


let mapStateToProps = function (state) {
    return {
        activeTask: state.app.activeTask
    }
}

let mapDispatchToProps = function (dispatch) {
    return {
        toggleShowSideNav:()=>dispatch(DomActions.toggleSideNav())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TopNavigation);
