import Base from './base_component';

export default class FormComponent extends Base {
    constructor(props) {
        super(props);
        this.state.form = {}; //setting up empty form
    }

    handleChange(e) {
        let form = this.state.form;
        form[e.target.name] = e.target.value;
        this.setState({form: form});
    }
}
