export default class TaskActions {

    add(description, estimate,playbookId) {
        return {
            type: "TASK_ADD",
            description,
            estimate,
            playbookId
        }
    }

    static start(task,playbook){
        return dispatch=>{
            window.FB.startTask(playbook,task,()=>{dispatch({type: "TASK_START", task:task});});
        };
    }

    pause(id) {
        return {
            type: "TASK_PAUSE",
            id
        }
    }

    static done(task,playbook){
        return dispatch=>{
            window.FB.markTaskDone(playbook,task);
      };
    }

    static delete(task, playbook){
        return dispatch=>{
            window.FB.deleteTask(playbook,task)
        }
    }

    static update(task, playbook, callback){
        return dispatch=>{
            window.FB.updateTask(playbook,task).then(callback)
        }
    }

}
