import {MDBBtn, MDBInput, MDBModal, MDBModalBody, MDBModalFooter, MDBModalHeader} from "mdbreact";
import React, {Component, Fragment} from "react";
import DateFormatter from "./date_formatter";
import DatePicker from "react-datepicker";
import {connect} from "react-redux";
import LapActions from "../../actions/laps_actions";

class LapModel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            editing: false,
            ...this.props.lap
        }
    }

    componentDidMount() {
        this.setState({editing:false})
    }

    static getDerivedStateFromProps(props,state){
        return {
            start : state.start || (props.lap && props.lap.start) || null,
            end : state.end || (props.lap && props.lap.end) || null,
            note : state.note || (props.lap && props.lap.note) || null
        }
    }

    renderDetails() {
        let state = this.state
        return (<Fragment>
            <div>Start : <DateFormatter milliseconds={this.state.start}/></div>
            <div>End : <DateFormatter milliseconds={this.state.end}/></div>
            <div>Note : {this.state.note}</div>
        </Fragment>)
    }

    handleChange = name => value => {
        let state = {};
        state[name] = value;
        this.setState(state);
    }

    handleEdit = e => this.setState({editing: true});
    handleSave = e => {
        let lap = {};
        lap.key = this.props.lap.key;
        lap.start = this.state.start;
        lap.end = this.state.end;
        lap.note = this.state.note;
        this.props.editLap(this.props.playbook, this.props.task, lap,()=>{
            this.setState({editing:false})
        })
    };

    renderToolBar(props) {
        return <Fragment>
            <MDBBtn color="secondary" onClick={()=>this.setState({editing:false},props.toggle)}>Close</MDBBtn>
            {this.props.task.status !== 'done' ? !this.state.editing ? <MDBBtn onClick={this.handleEdit} color="primary">Edit</MDBBtn> :
                <MDBBtn color="primary" onClick={this.handleSave}>Save</MDBBtn> : null}
        </Fragment>

    }

    renderForm() {
        let state = this.state;
        return (<Fragment>
            <div>Start : <DatePicker
                selected={this.state.start && new Date(this.state.start)}
                showTimeSelect
                onChange={val=>(this.handleChange('start')(val.getTime()))}
                dateFormat="dd/MM/yyyy hh:mm aa"
            />
            </div>
            <div>End : <DatePicker
                selected={this.state.end && new Date(this.state.end)}
                showTimeSelect
                onChange={val=>(this.handleChange('end')(val.getTime()))}
                dateFormat="dd/MM/yyyy hh:mm aa"
            />
            </div>
            <MDBInput valueDefault={this.state.note} onChange={e =>(this.handleChange('note')(e.target.value))} type="textarea" label="Note" rows="3"/>
        </Fragment>)
    }

    render() {
        let props = this.props;
        return props.lap !== null && (<MDBModal centered isOpen={true} size={'md'} toggle={()=>this.setState({editing:false},props.toggle)}>
            <MDBModalHeader toggle={()=>this.setState({editing:false},props.toggle)}>Lap Details</MDBModalHeader>
            <MDBModalBody>
                <div>Playbook : {props.playbook.title}</div>
                <div>Task : {props.task.title}</div>
                {this.state.editing?this.renderForm(props):this.renderDetails(props)}
            </MDBModalBody>
            <MDBModalFooter>
                {this.renderToolBar(props)}
            </MDBModalFooter>
        </MDBModal>);
    }
}

export default connect (null, {editLap:LapActions.edit})(LapModel);
