import React, {Component} from 'react';
import {Fa, ListGroup, ListGroupItem} from 'mdbreact';
import connect from "react-redux/es/connect/connect";
import DomActions from '../actions/dom_actions';
import {Link} from "react-router-dom";

class SideNavigation extends Component {

    constructor(props) {
        super(props)
        this.state = {app_version: window.APP_VERSION || null, isOpen: false}
        this.handleToggl = this.handleToggl.bind(this)
    }

    componentDidMount() {
        if (this.state.app_version !== "null") {
            setTimeout(() => {
                this.setState({app_version: window.APP_VERSION || "dev"})
            }, 1000)
        }
    }

    handleToggl = ()=> {
        this.props.hideSideNav()
        this.setState({
            isOpen: !this.state.isOpen
        });
    };

    render() {
        let classNames = 'hide-on-small sidebar-fixed';
        if (this.props.showSideNavOnSmallScreens) {
            classNames = 'sidebar-fixed';
        }
        return (<div className={classNames}>
                <a href="#!" className="logo-wrapper waves-effect">
                    <img alt="MDB React Logo" className="img-fluid"
                         src={process.env.PUBLIC_URL + '/img/taestitrack.png' }/>
                </a>
                <ListGroup className="list-group-flush">

                    <ListGroupItem className="cursor-pointer">
                        <Link to={"/dashboard"} onClick={this.handleToggl}>
                            <Fa icon="pie-chart" className="mr-3"/>
                            Dashboard
                        </Link>
                    </ListGroupItem>
                    <ListGroupItem className="cursor-pointer">
                        <Link to="/playbooks" onClick={this.handleToggl}>
                            <Fa icon="user" className="mr-3"/>
                            <span>Show Playbooks</span>
                        </Link>
                    </ListGroupItem>
                    <ListGroupItem className="cursor-pointer">
                        <Link to="/playbooks/new" onClick={this.handleToggl}>
                            <Fa icon="table" className="mr-3"/>
                            <span>Create Playbook</span>
                        </Link>
                    </ListGroupItem>
                    <ListGroupItem className="cursor-pointer">
                        <Link to="/logout" onClick={this.handleToggl}>
                            <Fa icon="sign-out" className="mr-3"/>
                            <a>Sign Out</a>
                        </Link>
                    </ListGroupItem>
                    <hr/>
                    <p id="app-version">{"Version : " + this.state.app_version}</p>
                </ListGroup>
            </div>
        );
    }
}


let mapStateToProps = function (state) {
    return {
        showSideNavOnSmallScreens: state.dom.show_sidenav_onsmallscreen
    }
};

let mapDispatchToProps = function (dispatch) {
    return {
        hideSideNav: () => dispatch(DomActions.hideSideNav())
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(SideNavigation);
