import React, {Fragment} from "react";
import {connect} from "react-redux";

import Base from "../base_components/base_component";
import NewTaskForm from './new_task_form';
import Task from './task';
import {MDBCol, MDBContainer, MDBRow} from "mdbreact";
import {withRouter} from "react-router";

const counter = {val:0, playListLoaded:false}
class Playbook extends Base {

    constructor(props) {
        super(props);
        this.state = {isTodoSelected: true,playbook:null};
        this.mobileTabToggle = this.mobileTabToggle.bind(this);
        counter.val = 0
        counter.playListLoaded = false;
    }

    static getDerivedStateFromProps(props) {
        let playbooks = props.playbooks;
        let playbook = playbooks[props.match.params.id] || null
        if(playbook){
            counter.playListLoaded = true;
        }
        return {
            playbook: playbook
        }
    }

    componentDidMount() {
        this.setState({listener: window.FB.registerPlaybookListener(this.props.match.params.id)});
    }

    componentWillUnmount() {
        window.FB.deRegisterPlaybookListener(this.state.playbook.key, this.state.listener);
    }

    renderTodo() {
        if (!this.state.playbook.tasks) {
            return null
        }
        let tasks = this.state.playbook.tasks;
        let taskKeys = Object.keys(tasks).filter(key => (tasks[key].status === 'new' || tasks[key].status === 'paused'));
        return taskKeys.map(key => {
            let task = tasks[key];
            task.key = key;
            return <Task key={key} task={task}/>

        });
    }

    renderDone() {
        if (!this.state.playbook.tasks) {
            return null
        }
        let tasks = this.state.playbook.tasks;
        let taskKeys = Object.keys(tasks).filter(key => tasks[key].status === 'done');
        return taskKeys.map(key => {
            let task = tasks[key];
            task.key = key;
            return <Task key={key} task={task}/>
        });
    }

    mobileTabToggle() {
        this.setState({isTodoSelected: !this.state.isTodoSelected});
    }

    render() {
        if(this.state.playbook === null){
            return "Loading playbook ..."
        }
        return <Fragment>
            <MDBContainer fluid className="d-none d-md-block">
                <MDBRow>
                    <MDBCol>
                        <h4>{this.state.playbook.title}</h4>
                    </MDBCol>
                </MDBRow>
                <MDBRow>
                    <MDBCol>
                        {this.state.playbook.description}
                    </MDBCol>
                </MDBRow>
                <MDBRow>
                    <MDBCol size="12" md="6" className="todo">
                        <h4>To-Do</h4>
                        <div className="spacer-40px"></div>
                        <MDBContainer fluid className="playbook-col todo">
                            <NewTaskForm playbook={this.state.playbook}/>
                            {this.renderTodo()}
                        </MDBContainer>
                    </MDBCol>
                    <MDBCol size="12" md="6">
                        <h4>Done</h4>
                        <div className="spacer-60px"></div>
                        <MDBContainer fluid className="playbook-col done">
                            {this.renderDone()}
                        </MDBContainer>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>

            <MDBContainer className="d-block d-md-none">
                <MDBRow size="12">
                    <MDBCol>
                        <h4>{this.state.playbook.title}</h4>
                        {this.state.playbook.description}
                    </MDBCol>
                </MDBRow>
                <MDBRow>
                    <ul className="nav nav-tabs col-12" id="myTab" role="tablist">
                        <li className="nav-item col-6">
                            <span onClick={this.mobileTabToggle}
                                  className={this.state.isTodoSelected ? "nav-link active" : "nav-link"}><h4>To-Do</h4></span>
                        </li>
                        <li className="nav-item col-6">
                            <span onClick={this.mobileTabToggle}
                                  className={this.state.isTodoSelected ? "nav-link" : "nav-link active"}><h4>Done</h4></span>
                        </li>
                    </ul>
                </MDBRow>
                <MDBRow>
                    <MDBCol size="12">
                        <div className="playbook-col">
                            {(() => {
                                let todo = <Fragment>
                                    <NewTaskForm playbook={this.state.playbook}/>
                                    {this.renderTodo()}
                                </Fragment>;

                                let done = <Fragment>
                                    {this.renderDone()}
                                </Fragment>;
                                return this.state.isTodoSelected ? todo : done;
                            })()}
                        </div>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Fragment>
    }
}

let mapStateToProps = function (state) {
    return {
        playbooks: state.playbooks,
    }
};

let mapDispatchToProps = function () {
    return {}
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Playbook));
