const actions = [
    "TOGGLE_SIDENAV",
    "HIDE_SIDENAV",
];

const actionHandlers = {
    TOGGLE_SIDENAV : toggleSidenav,
    HIDE_SIDENAV : hideSidenav,
}

function toggleSidenav(state) {
    state.show_sidenav_onsmallscreen = !state.show_sidenav_onsmallscreen;
    return state;
}

function hideSidenav(state) {
    state.show_sidenav_onsmallscreen = false;
    return state;
}

function clone(data) {
    return JSON.parse(JSON.stringify(data));
}

function isApplicable(action) {
    return actions.indexOf(action) > -1
}

export default function dom(state = {show_sidenav_onsmallscreen:false}, action) {
    let clonedState =clone(state);
    if(isApplicable(action.type)){
        return actionHandlers[action.type](clonedState,action);
    }
    return clonedState;
}

