import PlaybookActions from './actions/playbooks_actions';
import LogsActions from "./actions/logs_actions";

export default class FirebaseHelper {

    constructor() {
        this.db = window.firebase.database()
    }

    addNewPlayBook(title, description, next) {
        let refs = {};

        let newPlaybookForUser = cb => {
            let ref = null;
            if (window.user && window.user.uid) {
                ref = this.db.ref('users/' + window.user.uid + '/playbooks');
                let playbook = ref.push();
                refs.userPlabooksEntry = playbook;
                cb(playbook)
            }
        };


        let addToPlaybookRef = playbookUserRef => {
            let users = {};
            users[window.user.uid] = "owner";
            let status = "active";
            let data = {
                title,
                description,
                users,
                status
            }
            let ref = this.db.ref('/playbooks/' + playbookUserRef.key);
            refs.playbooksEntry = ref;
            ref.set(data).then(() => {
                playbookUserRef.set(true);
                refs.playbooksEntry.once('value').then((snap) => {
                    next(snap)
                }).catch(() => console.log("there is an error"));
            });
        }

        newPlaybookForUser(addToPlaybookRef.bind(this));
    }

    getPlaybooks() {
        let ref = this.db.ref('users/' + window.user.uid + '/playbooks');
        ref.once('value').then(snap => {
        }).catch(err => alert(err.message))
    }


    registerListeners() {
        window.FB_Listenters = []
        window.FB_Listenters[0] = this.db.ref('users/' + window.user.uid + '/playbooks').on('value', this.listenForUserPlaybooks.bind(this));
        window.FB_Listenters[1] = this.db.ref('users/' + window.user.uid + '/active-task').on('value', this.listenForActiveTask);
        window.FB_Listenters[1] = this.db.ref('users/' + window.user.uid + '/logs').on('value', this.listenForLogs);
    }

    listenForLogs(snapshot){
        let logs = snapshot.toJSON();
        window.APP_GLOBALS.store.dispatch(LogsActions.refresh(logs));
    }

    listenForUserPlaybooks(snapshot) {
        let playbooks = snapshot.toJSON();
        let books = {};
        if (playbooks == null) {
            return window.APP_GLOBALS.store.dispatch(PlaybookActions.hydrate(books));
        }
        let bookKeys = Object.keys(playbooks);

        let index = 0;
        let db = this.db;

        let processKey = () => {
            if (index <= (bookKeys.length - 1)) {
                db.ref('playbooks/' + bookKeys[index]).once('value').then((snap) => {
                    let key = snap.key
                    if (playbooks[key]) {
                        snap = snap.toJSON();
                        snap.key = key;
                        books[key] = snap;
                    }
                    processKey();
                }).catch((err) => {
                    console.log(err.message);
                    processKey();
                });
            } else {
                window.APP_GLOBALS.store.dispatch(PlaybookActions.hydrate(books));
            }
            index++;
        }
        processKey();
    }

    listenForActiveTask(snapshot) {
        let activeTask = snapshot.toJSON();
        window.APP_GLOBALS.store.dispatch({
            type: "SET_ACTIVE_TASK",
            activeTask
        });
    }

    listenForPlaybook(snapshot) {
        let playbook = snapshot.toJSON()
        if (playbook == null) {
            return
        }
        playbook.key = snapshot.key;
        playbook.ref = snapshot.ref;
        window.APP_GLOBALS.store.dispatch(PlaybookActions.update(playbook));
        window.APP_GLOBALS.store.dispatch({type:"CURRENT_PLAYBOOK",playbook});
    }

    registerPlaybookListener(key) {
        return this.db.ref("playbooks/" + key).on("value", this.listenForPlaybook);
    }

    deRegisterPlaybookListener(key, listener) {
        return this.db.ref("playbooks/" + key).off("value", listener)
    }

    addNewTask(playbook, task) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks");
        ref.push(task)
    }

    deleteTask(playbook, task){
        let taskRef = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key)
        return taskRef.remove();
    }

    markTaskDone(playbook, task, callback) {
        let taskStatusRef = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/status");
        return taskStatusRef.set("done").then(() => {
            this.endLap(playbook, task, (playbook,task)=>this.updateDoneLog(playbook,task,callback));
        });
    }

    updateTask(playbook, task){
        let taskRef = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key);
        delete task.key
        return taskRef.set(task);
    }

    updateDoneLog(playbook, task, callback) {
        let doneLogsRef = this.db.ref('users/' + window.user.uid + '/logs/completedTasks');
        let taskRef = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key);
        let data = {
            playbook: playbook.title,
            task: task.title,
        }
        taskRef.once('value').then(snap => {
            if(snap.child('status').val() === 'done'){
                let laps = snap.child('laps').val();
                let taskEndTime = 0;
                let taskStartTime = Number.MAX_SAFE_INTEGER;
                let taskDuration = 0;
                for(let lapKey in laps){
                    if(taskEndTime < laps[lapKey].end){
                        taskEndTime = laps[lapKey].end;
                    }
                    if(taskStartTime > laps[lapKey].start){
                        taskStartTime = laps[lapKey].start;
                    }
                    taskDuration += laps[lapKey].end - laps[lapKey].start;
                }
                data.started_at = taskStartTime;
                data.completed_at = taskEndTime;
                data.duration = parseInt(taskDuration/1000);
                data.estimate = snap.child('estimate').val();
                data.created_at = snap.child('created_at').val();
            }
            doneLogsRef.push(data).then(()=>{callback && callback();})
        })
    }

    editLap(playbook, task, lap, callback) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/laps/"+lap.key);
        let newLap = {};
        newLap.start = lap.start;
        newLap.end = lap.end;
        if(lap.note){
            newLap.note = lap.note;
        }
        ref.set(newLap).then(callback);
    }

    startLap(playbook, task, callback) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/laps");
        ref.once('value').then((snap) => {
            let laps = snap.toJSON();
            let ongoingLap = null;

            if (laps === null) {
                ref.push({
                    start: window.firebase.database.ServerValue.TIMESTAMP,
                    end: false
                });
                return callback();
            }
            //find any ongoing lap in lap list
            for (let lapKey in laps) {
                let lap = laps[lapKey];
                lap.key = lapKey;
                if (lap.end === false) {
                    ongoingLap = lap;
                }
            }

            if (ongoingLap === null) {
                ref.push({
                    start: window.firebase.database.ServerValue.TIMESTAMP,
                    end: false
                });
                return callback();
            }

            return callback();
        }).catch(() => console.log("there is error in starting lap"));
    }

    endLap(playbook, task, callback) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/laps");
        ref.once('value').then((snap) => {
            let laps = snap.toJSON();
            let ongoingLap = null;
            //task is marked done with no ongoing laps
            if (laps === null) {
                ref.push({
                    start: window.firebase.database.ServerValue.TIMESTAMP,
                    end: window.firebase.database.ServerValue.TIMESTAMP
                });
                return callback(playbook, task);
            }

            for (let lapKey in laps) {
                let lap = laps[lapKey];
                lap.key = lapKey;
                if (lap.end === false) {
                    ongoingLap = lap;
                }
            }

            if (ongoingLap === null) {
                return ref.push({
                    start: window.firebase.database.ServerValue.TIMESTAMP,
                    end: window.firebase.database.ServerValue.TIMESTAMP
                }).then(() => (callback(playbook, task)));
            }

            let ongoingRef = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/laps/" + ongoingLap.key + "/end");
            return ongoingRef.set(window.firebase.database.ServerValue.TIMESTAMP).then(() => callback(playbook, task));

        }).catch((err) => console.log("there is error in starting lap" + err));
    }

    markTaskOngoing(playbook, task) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/status");
        return ref.once('value').then(snap => {
            if (snap.val() === "new" || snap.val() === 'paused') {
                return ref.set("ongoing").then(() => {
                    this.startLap(playbook, task)
                });
            }
            return;
        })
    }

    markTaskPaused(playbook, task, callback) {
        let ref = this.db.ref("/playbooks/" + playbook.key + "/tasks/" + task.key + "/status");
        return ref.once('value').then(snap => {
            if (snap.val() === "ongoing") {
                return ref.set("paused").then(() => {
                    this.endLap(playbook, task, callback)
                });
            }
            return;
        })
    }

    startTask(playbook, task) {
        const refStr = 'users/' + window.user.uid + '/active-task';
        this.db.ref(refStr).once('value').then(snap => {
            let activeTask = snap.toJSON();
            if (activeTask === null) {
                this.db.ref(refStr).set({task: task.key, playbook: playbook.key}).then(() => {
                    return this.markTaskOngoing(playbook, task);
                });
            }
            return;
        });
    }

    pauseTask(playbook, task, callback) {
        const refStr = 'users/' + window.user.uid + '/active-task';
        this.db.ref(refStr).once('value').then(snap => {
            let activeTask = snap.toJSON();
            if (activeTask !== null) {
                this.db.ref(refStr).set(null).then(() => {
                    return this.markTaskPaused(playbook, task, callback);
                });
            }
            return;
        });
    }

    doneWithTask(playbook, task, callback) {
        const refStr = 'users/' + window.user.uid + '/active-task';
        this.db.ref(refStr).once('value').then(snap => {
            let activeTask = snap.toJSON();
            if (activeTask !== null) {
                this.db.ref(refStr).set(null).then(() => {
                    return this.markTaskDone(playbook, task, callback);
                });
            }
            return;
        });
    }

    removePlaybook(playbookId) {
        const refStr = 'users/' + window.user.uid + '/playbooks/' + playbookId
        this.db.ref(refStr).remove()
            .then(() => {
                let playbookRef = 'playbooks/' + playbookId;
                this.db.ref(playbookRef).remove()
                    .then(() => {
                        const refStr = 'users/' + window.user.uid + '/active-task';
                        this.db.ref(refStr).once('value').then(snap => {
                            let activeTask = snap.toJSON();
                            if (activeTask !== null && activeTask.playbook === playbookId) {
                                this.db.ref('users/' + window.user.uid + '/active-task').set(null);
                            }
                        })
                    })
                    .catch(() => console.error("delete failed"))
            })
            .catch(function (error) {
                console.log("Remove failed: " + error.message)
            });
    }

    serverTimeStamp() {
        const refStr = 'users/' + window.user.uid + '/current-time';
        this.db.ref(refStr).set(window.firebase.database.ServerValue.TIMESTAMP).then(() => {
            this.db.ref(refStr).once('value').then(snap => {
                window.APP_GLOBALS.TIMESTAMP = parseInt(snap.val() / 1000) + 2;
                setInterval(() => {
                    ++window.APP_GLOBALS.TIMESTAMP
                }, 1000);
            });
        })
    }


}
