const actions = [
    "REFRESH_LOGS",
];

const actionHandlers = {
    REFRESH_LOGS : refreshLogs,
}

function refreshLogs(state,action) {
    state = action.logs;
    return state;
}

function isApplicable(action) {
    return actions.indexOf(action) > -1
}

function clone(data) {
    return JSON.parse(JSON.stringify(data));
}

export default function logs(state = {logs:{}}, action) {
    let clonedState =clone(state);
    if(isApplicable(action.type)){
        return actionHandlers[action.type](clonedState,action);
    }
    return clonedState;
}

