import tasks from './tasks';

const actions = [
    "PLAYBOOK_HYDRATE",
    "PLAYBOOK_ADD",
    "PLAYBOOK_FILTER",
    "TASK_ADD",
    "PLAYBOOK_UPDATE"
];

const actionHandlers = {
    PLAYBOOK_ADD: handleNew,
    PLAYBOOK_FILTER: handleFilter,
    TASK_ADD: handleAddTask,
    PLAYBOOK_HYDRATE: handleHydrate,
    PLAYBOOK_UPDATE: handleUpdate
}

function handleNew(state, action) {
    let playbook = {
        description: action.description,
        title: action.title,
        status: "new",
        tasks: [],
        created_at: parseInt(Date.now() / 1000)
    }
    return [...state, playbook];
}

function handleFilter(state, action) {
    return state;
}

function handleAddTask(state, action) {
    return state.map((playbook) => {
        if (playbook.id === action.playbookId) {
            playbook.tasks = tasks(playbook.tasks, action);
        }
        return playbook;
    });
}

function handleHydrate(state, action) {
    return action.data;
}

function handleUpdate(state, action) {
    if(state[action.playbook.key]){
        state[action.playbook.key] = action.playbook;
    }
    return state;
}

function isApplicable(action) {
    return actions.indexOf(action) > -1
}


function clone(data) {
    return JSON.parse(JSON.stringify(data));
}

export default function playbooks(state = [], action) {
    let clonedState = clone(state);
    if (isApplicable(action.type)) {
        return actionHandlers[action.type](clonedState, action);
    }

    for(let key in clonedState){
        clonedState[key].tasks = tasks(clonedState[key].tasks, action)
    }

    return clonedState;
}

