import React from "react";
import {connect} from 'react-redux';

import Form from "../base_components/form_base_component";
import PlaybookActions from '../../actions/playbooks_actions'
import {MDBCol, MDBContainer, MDBJumbotron, MDBRow, MDBInput, MDBBtn} from "mdbreact";
import {withRouter} from "react-router";


class NewPlaybookForm extends Form {
    constructor(props) {
        super(props);
        this._bind(['onSubmit', 'handleChange']);
    }

    onSubmit(e) {
        e.preventDefault();
        // component delegation
        return this.props.submit && this.props.submit(this.state.form, playbook=>this.props.history.push(`/playbook/${playbook.key}`));
    }

    handleChange(e) {
        let form = this.state.form;
        form[e.target.name] = e.target.value;
        this.setState({form: form});
    }

    render() {
        return  <MDBContainer>
                <MDBRow>
                    <MDBCol>
                        <MDBJumbotron>
                            <h2>New Playbook</h2>
                            <form onSubmit={this.onSubmit}>
                                <MDBInput label="Playbook Title" name="title" onChange={this.handleChange}/>
                                <MDBInput label="Playbook Description" name="description" onChange={this.handleChange}/>
                                <MDBBtn color="green" onClick={this.onSubmit}>Create New Playbook</MDBBtn>
                            </form>
                        </MDBJumbotron>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>;
    }
}

const   mapDispatchToProps =  function(dispatch){
    return {
        submit: (form, callback) => {
            PlaybookActions.add(form.title,form.description,callback)
        }
    }
}
export default connect(null,mapDispatchToProps)(withRouter(NewPlaybookForm));
