import React, {Fragment} from 'react';
import {connect} from "react-redux";
import {
    Card,
    CardBody,
    CardFooter,
    CardHeader,
    CardText,
    CardTitle,
    Col,
    MDBBadge,
    MDBBtn,
    MDBIcon,
    MDBInput,
    MDBTable,
    Row,
} from 'mdbreact';

import TimeFormatter from './time_formatter';
import DateFormatter from './date_formatter';
import Base from '../base_components/base_component';
import TaskActions from '../../actions/task_actions';
import LapModal from './lap_modal'

class Task extends Base {

    constructor(props) {
        super(props);
        this.state = {
            lapsToggle: false,
            editLap: null,
            editTask: false,
            taskTitle: props.task.title
        };

    }

    onDelete = event => {
        event.preventDefault();
        const task = this.props.task
        this.props.delete(task, this.props.playbook);
    }

    onStartProvider() {
        return () => {
            this.props.start(this.props.task, this.props.playbook);
        }
    }

    onDoneProvider() {
        return () => {
            this.props.done(this.props.task, this.props.playbook);
        }
    }

    toggleLaps = () => {
        this.setState({lapsToggle: !this.state.lapsToggle})
    }

    onUpdate = () => {
        let task = {...this.props.task}
        task.title = this.state.taskTitle;
        this.props.update(task, this.props.playbook, () => {
            this.setState({editTask: false})
        });
    }

    renderControlPanel() {
        let startBtn = <MDBBtn color="cyan" size="sm" onClick={this.onStartProvider()}><MDBIcon icon="play"/></MDBBtn>;
        let endBtn = <MDBBtn color="green" size="sm" onClick={this.onDoneProvider()}><MDBIcon icon="stop"/></MDBBtn>;
        let lapsBtn = <MDBBtn color="yellow" size="sm" onClick={this.toggleLaps}><MDBIcon icon="list-alt"/></MDBBtn>;
        if (this.props.task.status === 'new') {
            lapsBtn = null;
        }
        if (this.props.task.status === 'done') {
            startBtn = null;
            endBtn = null;
        }
        return <CardFooter>
            {startBtn}
            {endBtn}
            {lapsBtn}
        </CardFooter>
    }

    getTimeTaken() {
        let task = this.props.task;
        let timeTaken = 0;
        if (task.laps) {
            for (let lapKey in task.laps) {
                let lap = task.laps[lapKey];
                if (lap.end && lap.start) {
                    timeTaken += lap.end - lap.start;
                }
            }
        }
        return timeTaken / 1000;
    }

    renderStatus(status) {
        switch (status) {
            case "new":
                return <MDBBadge color="blue">New</MDBBadge>;
            case "paused":
                return <MDBBadge color="yellow">Paused</MDBBadge>;
            case "done":
                return <MDBBadge color="green">Done</MDBBadge>;
            default:
                return <MDBBadge color="primary">{status}</MDBBadge>
        }
    }

    renderLaps() {
        if (!this.state.lapsToggle) {
            return;
        }
        let rows = [];
        let index = 1;
        for (let lapKey in this.props.task.laps) {
            let lap = {...this.props.task.laps[lapKey]};
            lap.key = lapKey;
            lap.title = <div>
                Worked for <TimeFormatter humanize
                                          timeInSeconds={parseInt((parseInt(lap.end) - parseInt(lap.start)) / 1000)}/> from <DateFormatter
                milliseconds={lap.start}/> {lap.note && ` on ${lap.note}`}.
            </div>


            rows.push(<tr key={lapKey}>
                <td><small>
                    <a onClick={this.showLapsModel(lap)}>
                        <MDBIcon icon="ellipsis-v"/>
                    </a>
                </small></td>
                <td>
                    <small>{index++}</small></td>
                <td>
                    <small>
                        <a onClick={this.showLapsModel(lap)}>
                            <em>{lap.title}</em>
                        </a>
                    </small>
                </td>
            </tr>)
        }
        return <div>
            <h4>Laps ({Object.keys(this.props.task.laps).length})</h4>
            <MDBTable responsive>
                {rows}
            </MDBTable>
        </div>

    }

    showLapsModel = lap => event => {
        event.preventDefault();
        this.setState({editLap: lap})
    }

    renderTitle() {
        if (this.state.editTask) {
            return <MDBInput label="Task description" type="textarea" value={this.state.taskTitle}
                             onChange={e => this.setState({taskTitle: e.target.value})}/>
        }
        return <Fragment>
            {this.props.task.title}
        </Fragment>
    }

    renderEditButton() {
        if(this.props.task.status === 'done'){
            return null;
        }

        if (this.state.editTask) {
            return (<Fragment>
                <a onClick={e => {
                    e.preventDefault();
                    this.onUpdate()
                }}
                ><MDBIcon className={'save link-icon'} icon={'save'}/></a>
                <a onClick={e => {
                    e.preventDefault();
                    this.setState({editTask: false})
                }}><MDBIcon className={'dismiss link-icon'} icon={'times'}/></a>
            </Fragment>)
        }

        return (<a onClick={e => {
            e.preventDefault();
            this.setState({editTask: true})
        }}><MDBIcon className={'edit link-icon'} icon={'edit'}/></a>)
    }

    render() {
        let task = this.props.task;
        let playbook = this.props.playbook;
        return (
            <Row style={{margin: "5px 0px 5px 0px"}} className={'task'}>
                <Col>
                    <Card>
                        <div className="face front">
                            <CardHeader className="img-fluid">
                                {this.renderStatus(task.status)}
                                <span className={'right top-right-controls'}>
                                {this.renderEditButton()}
                                    <a onClick={this.onDelete}><MDBIcon className={'delete link-icon'} icon={'trash'}/></a>
                            </span>
                            </CardHeader>
                            <CardBody>
                                <CardTitle className="task-title">{this.renderTitle()}</CardTitle>
                                <CardText>
                                    Estimate : <span className="task task-estimate"><TimeFormatter humanize
                                                                                                   timeInSeconds={task.estimate}/></span>
                                    Time Taken : <span className="task task-estimate"><TimeFormatter humanize
                                                                                                     timeInSeconds={this.getTimeTaken()}/></span>
                                    {this.renderLaps()}
                                </CardText>
                            </CardBody>
                            {this.renderControlPanel()}
                        </div>
                    </Card>
                </Col>
                {this.state.editLap !== null && <LapModal {...{task, playbook}} lap={this.state.editLap}
                                                          toggle={() => this.setState({editLap: null})}/>}
            </Row>)
    }


}

let mapStateToProps = function (state) {
    return {
        playbook: state.playbooks[state.app.currentPlaybook]
    }
};

let mapDispatchToProps = function (dispatch) {
    return {
        done: (task, playbook) => {
            dispatch(TaskActions.done(task, playbook));
        },
        start: (task, playbook) => {
            dispatch(TaskActions.start(task, playbook))
        },
        delete: (task, playbook) => {
            dispatch(TaskActions.delete(task, playbook))
        },
        update: (task, playbook, callback) => {
            dispatch(TaskActions.update(task, playbook, callback))
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Task);
