import React from 'react';
import Base from '../base_components/base_component';
import {MDBBtn, MDBCol, MDBContainer, MDBIcon, MDBRow} from 'mdbreact';
import TimeFormatter from "./time_formatter";
import {connect} from "react-redux";

class ActiveTask extends Base {
    constructor(props) {
        super(props);
        this.state = {
            remainingTime: 0,
        }
        this._bind(['pause', 'done']);
        this.timerId = null;
    }

    componentDidMount() {
        this.timerId = setInterval(this.updateRemainingTime,1000)
    }

    componentWillUnmount() {
        clearInterval(this.timerId)
    }

    updateRemainingTime = event=>{
       let remainingTime =  this.props.activeTask && this.props.activeTask.estimate - ActiveTask.getTimeTaken(this.props.activeTask);
       this.setState({remainingTime});
    }

    pause() {
        window.FB.pauseTask(this.props.playbook, this.props.activeTask, this.componentWillUnmount);
    }

    done() {
        window.FB.doneWithTask(this.props.playbook, this.props.activeTask, this.componentWillUnmount);
    }

   static getTimeTaken(task) {
        let timeTaken = 0;
        if (task.laps) {
            for (let lapKey in task.laps) {
                let lap = task.laps[lapKey];
                if (lap.end && lap.start) {
                    timeTaken += lap.end - lap.start;
                }
                if (lap.end === false && lap.start) {
                    timeTaken += (window.APP_GLOBALS.TIMESTAMP * 1000) - lap.start;
                }
            }
        }
        return timeTaken / 1000;
    }


    render() {
        let task = this.props.activeTask;
        let playbook = this.props.playbook;
        if (!(playbook && task)) {
            return null
        }
        return <MDBContainer fluid>
            <MDBRow middle className="active-task">
                <MDBCol size="2" className="active-task playbook-title">
                    Playbook : {playbook.title}
                </MDBCol>
                <MDBCol size="8">
                    <MDBRow top>
                        <MDBCol size="12" className="active-task active-timer">
                            <TimeFormatter timeInSeconds={this.state.remainingTime}/>
                        </MDBCol>
                    </MDBRow>
                    <MDBRow bottom>
                        <MDBCol size="12" className="active-task title">{task.title}</MDBCol>
                    </MDBRow>
                </MDBCol>
                <MDBCol size="2">
                    <MDBBtn color="cyan" size="sm" onClick={this.pause}><MDBIcon icon="pause"/></MDBBtn>
                    <MDBBtn color="green" size="sm" onClick={this.done}><MDBIcon icon="stop"/></MDBBtn>
                </MDBCol>
            </MDBRow>
        </MDBContainer>
    }
}

let mapStateToProps = function (state) {
    let activeTask = null
    let playbook = null
    if (state.app.activeTask && state.playbooks[state.app.activeTask.playbook]) {
        let taskKey = state.app.activeTask.task;
        let playbookKey = state.app.activeTask.playbook;
        activeTask = state.playbooks[playbookKey].tasks[taskKey];
        activeTask.key = taskKey;
        playbook = state.playbooks[playbookKey];
    }
    return {activeTask, playbook}
}

let mapDispatchToProps = function (dispatch) {
    return {}
}

export default connect(mapStateToProps, mapDispatchToProps)(ActiveTask);
