import React from 'react';
import Base from './base_components/base_component';
import {MDBBtn, MDBCol, MDBContainer, MDBInput, MDBJumbotron, MDBRow} from 'mdbreact';
import {withRouter} from "react-router";
import queryString from 'query-string';

export default withRouter( class SignIn extends Base {
    constructor(props) {
        super(props);
        this.state = {form: {}};
        this._bind(['change', 'login'])
    }

    change(e) {
        let name = e.target.name;
        this.state.form[name] = e.target.value;
        this.setState(this.state);
    }

    login(e) {
        e.preventDefault();
        window.firebase.auth().signInWithEmailAndPassword(this.state.form.email, this.state.form.password).catch(function (error) {
            let errorMessage = error.message;
            alert(errorMessage)
        }).then((user) => {
            window.user = user.user;
            window.FB.registerListeners();
            let redirectPath = atob(queryString.parse(this.props.location.search).redirect);
            if(redirectPath.length>1){
                this.props.history.push(redirectPath)
            }else{
                this.props.history.push('/playbooks')
            }
            window.FB.serverTimeStamp();
        });

    }

    signup = ()=>{
       this.props.history.push('/register')
    }

    resetPassword = event=>{
        event.preventDefault();
        this.props.history.push("/password/reset");
    }

    render() {
        return <MDBContainer className="mt-5 text-center">
            <MDBRow>
                <MDBCol>
                    <MDBJumbotron>
                        <div className="form signin-form">
                            <MDBInput name="email" onChange={this.change} label="Email"/>
                            <MDBInput name="password"  onChange={this.change} label="Password" type="password"/>
                            <MDBBtn color="dark-green" onClick={this.login}>Sign In</MDBBtn>
                            <MDBBtn color="blue" onClick={this.signup}>Sign Up</MDBBtn>
                            <br/>
                            <a href="#" onClick={this.resetPassword}>Forgot Password</a>
                        </div>
                    </MDBJumbotron>
                </MDBCol>
            </MDBRow>
        </MDBContainer>;
    }

})
