export default class PlaybookActions {
    static add(title, description, callback = () => null) {
        window.FB.addNewPlayBook(title, description, (snap) => {
            return setTimeout(function () {
                callback(snap)
            }, 0);
        });
    };

    static hydrate(books) {
        return {
            type: "PLAYBOOK_HYDRATE",
            data: books
        }
    }

    static update(playbook) {
        return {
            type: "PLAYBOOK_UPDATE",
            playbook: playbook
        }
    }
}
