import "@fortawesome/fontawesome-free/css/all.min.css";
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import 'mdbreact/dist/mdbreact';
import "react-datepicker/dist/react-datepicker.css";
import '@fortawesome/fontawesome-free/webfonts/fa-solid-900.woff';
import React from 'react';
import ReactDOM from 'react-dom';
import store from "./store";
import AppRouter from './routes';
import FirebaseHlpr from './firebase_helper';
window.APP_GLOBALS = {store: store({})};
window.FB = new FirebaseHlpr();
window.firebase.auth().onAuthStateChanged(user => {
    if (user) {
        window.user = user;
        window.FB.registerListeners();
        window.FB.serverTimeStamp();
    }
    ReactDOM.render(<AppRouter/>, document.getElementById('root'));
});
