const actions = [
    "TASK_ADD",
    "TASK_START",
    "TASK_DONE"
];

const actionHandlers = {
    TASK_ADD : handleNew
}

function handleNew(state,action) {
     delete(action.type)
     let task = {
         description : action.description,
         estimate : action.estimate,
         status : "new",
         interval : 0,
         created_at : parseInt(Date.now()/1000)
     }
     return [...state, task];
}


function isApplicable(action) {
    return actions.indexOf(action) > -1
}

function clone(data) {
    return JSON.parse(JSON.stringify(data));
}

export default function tasks(state = [], action) {
    if(isApplicable(action.type)){
        let clonedState = clone(state);
        return actionHandlers[action.type](clonedState,action);
    }
    return state;
}