import React from 'react';
import Base from './base_components/base_component';
import {MDBBtn, MDBCol, MDBContainer, MDBInput, MDBJumbotron, MDBRow} from "mdbreact";

export default class ResetPassword extends Base {
    constructor(props) {
        super(props);
        this.state = {form: {}};
        this._bind(['change', 'reset'])
        window.firebase.auth().languageCode = 'en';
    }


    change(e) {
        let name = e.target.name;
        let val = e.target.value;
        this.state.form[name] = val;
        this.setState(this.state);
    }

    reset(e) {
        e.preventDefault();
        let promise = window.firebase.auth().sendPasswordResetEmail(this.state.form.email);
            promise.then(()=>{
                alert("password reset Mail sent")
            })
            promise.catch(function (error) {
            // Handle Errors here.
            let errorCode = error.code;
            let errorMessage = error.message;
            alert(errorMessage)
            // ...
        });
    }

    componentDidMount() {
        this.recaptcha = new window.firebase.auth.RecaptchaVerifier("captcha")
        this.recaptcha.render()
    }

    render() {
        return <MDBContainer className="mt-5 text-center">
            <MDBRow>
                <MDBCol>
                    <MDBJumbotron>
                        <div className="form signin-form">
                            <MDBInput name="email" onChange={this.change} label="Email"/>
                            <div id="captcha"></div>
                            <MDBBtn color="dark-green" onClick={this.reset}>Reset</MDBBtn>
                        </div>
                    </MDBJumbotron>
                </MDBCol>
            </MDBRow>
        </MDBContainer>;
    }

}


