import React from 'react';
import Base from '../base_components/base_component';
import {
    Card,
    CardBody,
    CardFooter,
    CardHeader,
    CardText,
    CardTitle,
    Col,
    MDBBadge,
    MDBBtn,
    MDBIcon,
    MDBInput,
    Row
} from 'mdbreact';

export default class NewTaskForm extends Base {
    constructor(props) {
        super(props);
        this.state = {
            showFormFlag: false,
        };
        this._bind(["onAddClick", "onDiscardClick", "onClickCreateNew", "handleUpdateCard", "handleUpdateCardTime"]);
    }

    onDiscardClick(event) {
        this.setState({showFormFlag: false, newCard: null})
    }

    onAddClick() {
        this.setState({showFormFlag: false})
        let task = this.state.newCard
        let estimate = task.estimate.hrs ? task.estimate.hrs * 3600 : 0;
        estimate += task.estimate.min ? task.estimate.min * 60 : 0;
        estimate += task.estimate.sec ? task.estimate.sec * 1 : 0;
        task.estimate = estimate;
        task.created_at = window.firebase.database.ServerValue.TIMESTAMP;
        task.progress = {}
        window.FB.addNewTask(this.props.playbook, task);
    }

    onClickCreateNew() {
        this.setState({
            showFormFlag: true,
            newCard: {status: "new", estimate: {}}
        });
    }

    handleUpdateCard(event) {
        let newCard = this.state.newCard;
        newCard[event.target.dataset.key] = event.target.value;
        this.setState({newCard: newCard});
    }

    handleUpdateCardTime(event) {
        let card = this.state.newCard;
        card.estimate[event.target.dataset.key] = event.target.value;
        this.setState({newCard: card});
    }

    renderCreateNewButton() {
        return <Row style={{margin: "5px 0px 5px 0px"}} end>
            <Col md="5">
                <MDBBtn color="green" size="sm" onClick={this.onClickCreateNew}><MDBIcon icon="plus"/>
                    Create New
                </MDBBtn>
            </Col>
        </Row>;
    }

    renderCreateNewForm() {
        return <Row style={{margin: "5px 0px 5px 0px"}}>
            <Col>
                <Card>
                    <CardHeader className="img-fluid">
                        <MDBBadge color="blue">New</MDBBadge>
                    </CardHeader>
                    <CardBody>
                        <CardTitle className="task-title">
                            <MDBInput label="Task description" type="textarea" data-key="title" name="title"
                                      onChange={this.handleUpdateCard}/>
                        </CardTitle>
                        <CardText>
                            Estimate : <span className="task task-estimate">
                            <Row>
                                <Col size={4}>
                                    <MDBInput className="new-task-time-input" label="hh" data-key="hrs" type="number"
                                              size="2" min="0" max="23" onChange={this.handleUpdateCardTime}/>
                                </Col>
                                <Col size={4} >
                            <MDBInput className="new-task-time-input" label="mm" data-key="min" type="number" size="2"
                                      min="0" max="59" onChange={this.handleUpdateCardTime}/>
                                </Col>
                                <Col size={4}>
                                    <MDBInput className="new-task-time-input" label="ss" data-key="sec" type="number"
                                              size="2" min="0" max="59" onChange={this.handleUpdateCardTime}/>
                                </Col>
                            </Row>
                        </span>

                        </CardText>
                    </CardBody>
                    <CardFooter>
                        <MDBBtn color="red" onClick={this.onDiscardClick}>Discard</MDBBtn>
                        <MDBBtn color="green" onClick={this.onAddClick}>Add</MDBBtn>
                    </CardFooter>
                </Card>
            </Col>
        </Row>
    }

    render() {
        return this.state.showFormFlag ? this.renderCreateNewForm() : this.renderCreateNewButton();
    }


}
