import React from "react";
import {connect} from "react-redux";

import Base from "../base_components/base_component";

import {MDBCol, MDBContainer, MDBJumbotron, MDBRow, Table, TableBody, TableHead} from "mdbreact";
import TimeFormatter from "../playbook/time_formatter";
import DateFormatter from "../playbook/date_formatter";

class Dashboard extends Base {

    renderLogsTable() {
        let tasks = this.props.completedTasks;
        const renderLogsRows = () => {
            let rows = []
            let counter = 1
            for (let taskKey in tasks) {
                let task = tasks[taskKey];
                rows.push(<tr>
                    <th>{counter++}</th>
                    <th>{task.playbook}</th>
                    <th>{task.task}</th>
                    <th><DateFormatter milliseconds={task.created_at}/></th>
                    <th><DateFormatter milliseconds={task.started_at}/></th>
                    <th><DateFormatter milliseconds={task.completed_at}/></th>
                    <th><TimeFormatter color={"black"} timeInSeconds={task.estimate}/></th>
                    <th><TimeFormatter color={"black"} timeInSeconds={task.duration}/></th>
                    <th><TimeFormatter timeInSeconds={(parseInt(task.estimate) - parseInt(task.duration))}/></th>
                </tr>)
            }
            return rows
        }
        return <Table hover responsive scrollY maxHeight='50vh'>
            <TableHead color={"pink darken-3"} textWhite>
                <tr>
                    <th>#</th>
                    <th>Playbook Title</th>
                    <th>Task Title</th>
                    <th>Created On</th>
                    <th>Started On</th>
                    <th>Completed On</th>
                    <th>Estimate</th>
                    <th>Actual</th>
                    <th>Deviation</th>
                </tr>
            </TableHead>
            <TableBody>
                {renderLogsRows()}
            </TableBody>
        </Table>
    }


    render() {

        return <MDBContainer>
            <MDBRow>
                <MDBCol>
                    <MDBJumbotron>
                        <h1>Completed Tasks</h1>
                        {this.renderLogsTable()}
                    </MDBJumbotron>
                </MDBCol>
            </MDBRow>
        </MDBContainer>
    }
}

let mapStateToProps = function (state) {
    return {
        completedTasks: (state.logs && state.logs.completedTasks) || null
    }
}

export default connect(mapStateToProps, null)(Dashboard);
