import {BrowserRouter, Route, Switch} from 'react-router-dom';
import Dashboard from './components/dashboard/dashboard_component';
import NewPlaybook from './components/playbook/new_playbook_form';
import ListPlaybook from './components/playbook/playbook_list';
import {Provider} from "react-redux";
import TopNavigation from "./components/topNavigation";
import SideNavigation from "./components/sideNavigation";
import React from "react";
import SignIn from "./components/sign_in";
import SignUp from "./components/sign_up";
import ResetPassword from "./components/reset_password";
import Playbook from "./components/playbook/playbook";
import {Redirect, withRouter} from "react-router";

const getPage = function (Top, Side, Main) {
    return () => (<div className="flexible-content page-wrapper">
        <div className="left-panel">
            {Side}
        </div>
        <div className="right-panel">
            <div className="top-nav">
                {Top}</div>
            <div className="main-block">
                {Main}
            </div>
        </div>
    </div>);
}

const getPageWithNavigation = function (component) {
    return getPage(<TopNavigation/>, <SideNavigation/>, component);
}

const getProviderWrapped = function (component) {
    return () => <Provider store={window.APP_GLOBALS.store}>
        {component}
    </Provider>
}

export function MakePrivateRoute(Component){
    return ()=>{
        if(window.user){
            return <Component/>
        }
        let redirect = btoa(window.location.pathname)
        return <Redirect to={`/login?redirect=${redirect}`}/>
    }
}

const SignInPage = getPage(null, null, <SignIn/>)
const SignUpPage = getPage(null, null, <SignUp/>);
const ResetPasswordPage = getPage(null, null, <ResetPassword/>);
const DashboardPage = MakePrivateRoute(getPageWithNavigation(<Dashboard/>));
const NewPlaybookPage = MakePrivateRoute(getPageWithNavigation(<NewPlaybook/>));
const ListPlaybookPage = MakePrivateRoute(getPageWithNavigation(<ListPlaybook/>));
const PlaybookPage = MakePrivateRoute(getPageWithNavigation(<Playbook/>));

const Logout = withRouter(props => {
    window.firebase.auth().signOut().then(() => {
        return props.history.push('/login')
    })
    return "Logging out...";
})

export default getProviderWrapped(<BrowserRouter>
    <Switch>
        <Route path="/login"><SignInPage/></Route>
        <Route path="/logout"><Logout/></Route>
        <Route path="/register"><SignUpPage/></Route>
        <Route path="/password/reset"><ResetPasswordPage/></Route>
        <Route path="/dashboard"><DashboardPage/></Route>
        <Route path="/playbook/:id"><PlaybookPage/></Route>
        <Route path="/playbooks/new"><NewPlaybookPage/></Route>
        <Route path="/playbooks"><ListPlaybookPage/></Route>
        <Route path="/"><DashboardPage/></Route>
    </Switch>
</BrowserRouter>)