import React from "react";
import {connect} from "react-redux";
import Base from "../base_components/base_component";
import {MDBCol, MDBContainer, MDBIcon, MDBRow} from "mdbreact";
import {withRouter} from "react-router-dom";
import * as moment from "moment";

class PlaybookList extends Base {

    deletePlaybook(playbookKey) {
        return event => {
            event.stopPropagation();
            window.FB.removePlaybook(playbookKey)
        }
    }

    loadPlaybook = playbook => () => {
        this.props.launchPlaybook(playbook, () => this.props.history.push(`/playbook/${playbook.key}`));
    }

    renderList() {
        let playbooks = null;

        let playbookCard = (playbookKey, totalTasks, completedTasks, totalEstimates, playbook) => {
            return <div className={"card playbook-card"} key={playbookKey} onClick={this.loadPlaybook(playbook)}
                        style={{cursor: "pointer"}}>
                <div className={'playbook-card-title'} >{playbook.title}</div>
                <div className={'playbook-card-description'} >{playbook.description}</div>
                <div className={'playbook-card-text'}>Contains {totalTasks} tasks with total estimated time
                    of {moment.duration(totalEstimates, "second").humanize()}. {completedTasks} tasks are already
                    completed.
                </div>
                <div className={'playbook-card-delete'}>
                    <a onClick={this.deletePlaybook(playbookKey)}><MDBIcon className={'delete link-icon'}
                                                                           icon={'trash'}/></a>
                </div>
            </div>
        }

        if (this.props.playbooks) {
            playbooks = Object.keys(this.props.playbooks).map((playbookKey) => {
                let playbook = this.props.playbooks[playbookKey];
                let completedTasks = 0;
                let totalEstimates = 0;
                let totalTasks = 0
                if (playbook.tasks) {
                    let taskKeys = Object.keys(playbook.tasks);
                    taskKeys.forEach((key) => {
                        let task = playbook.tasks[key];
                        if (task.status === "done") {
                            completedTasks++;
                        }
                        totalEstimates += task.estimate;
                    })
                    totalTasks = taskKeys.length;
                }
                return playbookCard(playbookKey, totalTasks, completedTasks, totalEstimates, playbook);
            });
        }

        return <div className={"playbook-list"}>
            {playbooks}
        </div>
    }

    render() {

        return <MDBContainer>
            <MDBRow>
                <MDBCol>
                    <h1>Playbooks</h1>
                    {this.renderList()}
                </MDBCol>
            </MDBRow>
        </MDBContainer>
    }
}

let mapStateToProps = function (state) {
    return {
        playbooks: state.playbooks
    }
}

let mapDispatchToProps = function (dispatch) {
    return {
        launchPlaybook: function (playbook, callback) {
            dispatch({
                type: "CURRENT_PLAYBOOK",
                playbook
            });
            callback();
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(PlaybookList));
