import React from 'react';

export default class BaseComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    _bind(functionStringName) {
        if (!functionStringName) {
            return;
        }

        if (functionStringName.constructor === Array) {
            functionStringName.map((funcName) => {
                this[funcName] = this[funcName].bind(this);
            });
            return;
        }
        if (this[functionStringName].constructor === Function) {
            this[functionStringName] = this[functionStringName].bind(this);
            return;
        }
    }

}
