const actions = [
    "CURRENT_PLAYBOOK",
    "SET_ACTIVE_TASK"
];

const actionHandlers = {
    CURRENT_PLAYBOOK : currentPlaybook,
    SET_ACTIVE_TASK : setActiveTask
}

function currentPlaybook(state,action) {
    state.currentPlaybook = action.playbook.key;
    return state;
}

function setActiveTask(state, action){
    state.activeTask = action.activeTask
    return state;
}

function clone(data) {
    return JSON.parse(JSON.stringify(data));
}

function isApplicable(action) {
    return actions.indexOf(action) > -1
}

export default function app(state = {currentPlaybook:null, activeTask:null}, action) {
    let clonedState =clone(state);
    if(isApplicable(action.type)){
        return actionHandlers[action.type](clonedState,action);
    }
    return clonedState;
}

